<template lang="html">
  <div class="rule-notice-warn">
    <div class="rule-notice-title clearfix">
      <span class="left fl">我收到的违规记录</span>
      <span class="note-time fr">{{notice.createTime}}</span>
    </div>
    <div class="rule-notice-content">{{notice.content}}</div>
    <div class="rule-notice-button clearfix">
      <span class="fill-rate fl" v-if="false">补缴广告费</span>
      <!-- <span class="appeal fr">违规申诉</span> -->
      <span @click="$emit('appeal-item', notice)" class="appeal fl">违规申诉</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    notice: {
      type: Object
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.rule-notice-warn {
  padding: 0 .22rem;
  margin-top: .3rem;
  background: @whiteColor;
  .rule-notice-title {
    padding: .2rem 0 .12rem;
    .left {
      font-size: .32rem;
      line-height: .32rem;
      color: @redColor;
    }
    .note-time {
      font-size: .3rem;
      color: @lightColor;
    }
  }
  .rule-notice-content {
    font-size: .3rem;
    color: @midleColor;
    line-height: .52rem;
  }
  .rule-notice-button {
    padding: .26rem 0 .2rem;
    span {
      // display: inline-block;
      width: 3rem;
      height: .88rem;
      line-height: .88rem;
      text-align: center;
      font-size: .34rem;
      border-radius: .1rem;
      background: @activeColor;
      color: @whiteColor;
    }
  }
}
</style>



