<template lang="html">
  <div class="rule-notice-page">
    <!-- 头部 -->
    <default-head header="违规通知"></default-head>

    <rule-notice-list>
      <p slot="left">挂断后本机回拨</p>
      <div class="this clearfix" slot="right">
        <div class="this-left fl">
          <p>判断依据:</p>
          <p>解决方案:</p>
        </div>
        <div class="this-right fl">
          <p>挂断后，1分钟内回拨</p>
          <p>记录违规次数，给卖家发送违规通知，5次以上通知其补全相应费用作为广告费，否则封号。</p>
        </div>
      </div>
    </rule-notice-list>

    <!-- <rule-notice-list marginTop=".3rem">
      <p slot="left">挂断后用其他手机回拨</p>
      <div class="others" slot="right">
        <p>解决方案:</p>
        <p>1、将接通率的总评分权重加大</p>
        <p>2、买家举报有奖</p>
        <p>3、设置公告，非平台号码回拨都是骗局</p>
      </div>
    </rule-notice-list> -->

    <rule-notice-list marginTop=".3rem">
      <p slot="left" class="final">最终违规解决方案</p>
      <div class="final-solution" slot="right">
        <p>一切上线时间挂断电话或者不接通电话都判定为违规</p>
      </div>
    </rule-notice-list>
    <div class=""
      v-infinite-scroll="loadMore"
     :infinite-scroll-disabled="loading"
     :infinite-scroll-distance="10"
     :infinite-scroll-immediate-check="true"
    >
      <rule-notice-warn v-for="(item, index) in noticeArr" :key="index" @appeal-item="appealItem"
      :notice="item"></rule-notice-warn>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import _ from 'underscore'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import ruleNoticeList from '@/components/ruleNotice/ruleNoticeList'
import ruleNoticeWarn from '@/components/ruleNotice/ruleNoticeWarn'

export default {
  data () {
    return {
      noticeArr: [],
      loading: false,
      pageSize: 10,
      pageIndex: 1
    }
  },
  computed: {
    userId: function () {
      return utils.userId()
    }
  },
  mounted () {
    this.ajaxGetUserIllegal({userId: this.userId, pageSize: this.pageSize, pageIndex: this.pageIndex})
  },
  methods: {
    loadMore: _.throttle(function () {
      // 关闭无线滚动
      if (this.page >= this.total) {
        this.loading = true
        this.page++
        this.ajaxGetUserIllegal({userId: this.userId, pageIndex: this.page, pageSize: 10, callback: this.changeLoading})
      }
    }, 300),
    // 启动无线滚动
    changeLoading () {
      this.loading = false
    },
    appealItem (item) {
      let data = {
        updateTime: item.createTime,
        realName: item.realName,
        mobile: item.mobile,
        picArray: item.picArray,
        id: item.id,
        nickName: item.nickName,
        type: 2
      }
      sessionStorage.setItem('appealform', JSON.stringify(data))
      this.$router.push('/appealform')
    },
    ajaxGetUserIllegal ({userId = 0, pageSize = 10, pageIndex = 1, callback = null} = {}) {
      minutils.IndicatorOpen('加载中...')
      api.getUserIllegal({userId, pageSize, pageIndex}).then((response) => {
        minutils.IndicatorClose()
        let result = response.body
        typeof callback === 'function' && callback()
        if (result.isSuccess) {
          if (pageIndex === 1) {
            this.noticeArr = result.data.userIllegal
          } else {
            this.noticeArr = [...this.noticeArr, ...result.data.userIllegal]
          }
          this.total = result.data.total
          this.page = result.data.pageIndex
        }
      })
    }
  },
  components: { defaultHead, ruleNoticeList, ruleNoticeWarn }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.rule-notice-page {
  padding-top: .88rem;
  .this {
    .this-left {
      width: 1.5rem;
      line-height: .3rem;
      color: @midleColor;
      p {
        margin-bottom: .3rem;
      }
    }
    .this-right {
      width: 3.5rem;
      line-height: .3rem;
      p:first-child {
        margin-bottom: .19rem;
      }
      p:last-child {
        line-height: .52rem;
      }
    }
  }
  .others {
    width: 4.8rem;
    line-height: .52rem;
    padding-left: .6rem;
    text-indent: -.6rem;
    p:first-child {
      color: @midleColor;
    }
  }
  .final {
    text-align: left;
    width: 1.2rem;
  }
  .final-solution {
    width: 3.7rem;
    line-height: .52rem;
    padding-top: .8rem;
  }
}

</style>



