<template lang="html">
  <div class="rule-notice-list" :style="{marginTop: marginTop}">
    <div class="rule-notice-list-item clearfix">
      <div class="rule-notice-list-left fl">
        <slot name="left"></slot>
      </div>
      <div class="rule-notice-list-right fl">
        <slot name="right"></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    marginTop: {
      type: String,
      default: '0rem'
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.rule-notice-list {
  .rule-notice-list-item {
    height: 3rem;
    padding: .2rem .22rem .3rem;
    font-size: .3rem;
    background: @whiteColor;
    .rule-notice-list-left {
      width: 1.78rem;
      height: 100%;
      padding-top: .83rem;
      text-align: center;
      line-height: .47rem;
      border-right: .01rem solid @lineColor;
    }
    .rule-notice-list-right {
      margin-left: .22rem;
      height: 2.5rem;
    }
  }
}
</style>



